<template>
  <div>
    <div class="action">
      <el-input
        ref="addrTo"
        v-model="addressTransferTo"
        placeholder="转卡接收地址"
        style="width:370px"
      ></el-input>
      <el-button
        @click.prevent="transferBatch"
        type="primary"
        :loading="isTransfering"
        >转卡</el-button
      >
      <el-button
        @click.prevent="doLinggong"
        type="primary"
        :loading="isDoLinggong"
        >打零工</el-button
      >
      <el-button @click.prevent="claim" type="primary" :loading="isGetRewarding"
        >一键收菜</el-button
      >
      <el-button
        @click.prevent="quitwork"
        type="primary"
        :loading="isQuitWorking"
        >退出工作</el-button
      >
    </div>
    <el-table :data="heros" stripe @selection-change="handleSelectionChange">
      <el-table-column type="selection" width="40" />
      <el-table-column label="序号" width="50">
        <template #default="scope">
          {{ scope.$index + 1 }}
        </template>
      </el-table-column>
      <el-table-column
        label="工作状态"
        :filters="[
          { text: '工作中', value: '工作中' },
          { text: '休息', value: '休息' },
        ]"
        :filter-method="filterHandler"
      >
        <template #default="scope">
          {{ scope.row.isWorking ? "工作中" : "休息" }}
        </template>
      </el-table-column>
      <el-table-column label="工作类型">
        <template #default="scope">
          {{ scope.row.isWorking ? scope.row.workTypeName : "" }}
        </template>
      </el-table-column>
      <el-table-column label="开始区块">
        <template #default="scope">
          {{ scope.row.isWorking ? scope.row.workInfo.startTime : -1 }}
        </template>
      </el-table-column>
      <el-table-column label="当前收益">
        <template #default="scope">
          {{ scope.row.isWorking ? scope.row.income : 0 }}
        </template>
      </el-table-column>
      <el-table-column label="英雄ID" width="90">
        <template #default="scope">
          {{ shortId(scope.row["tokenId"]) }}
        </template>
      </el-table-column>
      <el-table-column
        label="类型"
        :filters="[
          { text: '战士', value: '战士' },
          { text: '盗贼', value: '盗贼' },
          { text: '法师', value: '法师' },
          { text: '游侠', value: '游侠' },
        ]"
        :filter-method="filterHandler"
        width="70"
      >
        <template #default="scope">
          {{ scope.row["roleType"] }}
        </template>
      </el-table-column>
      <el-table-column label="力量" width="50">
        <template #default="scope">
          {{ scope.row[0][0] }}
        </template>
      </el-table-column>
      <el-table-column label="敏捷" width="50">
        <template #default="scope">
          {{ scope.row[0][1] }}
        </template>
      </el-table-column>
      <el-table-column label="体质" width="50">
        <template #default="scope">
          {{ scope.row[0][2] }}
        </template>
      </el-table-column>
      <el-table-column label="意志" width="50">
        <template #default="scope">
          {{ scope.row[0][3] }}
        </template>
      </el-table-column>
      <el-table-column label="智力" width="50">
        <template #default="scope">
          {{ scope.row[0][4] }}
        </template>
      </el-table-column>
      <el-table-column label="精神" width="50">
        <template #default="scope">
          {{ scope.row[0][5] }}
        </template>
      </el-table-column>
      <el-table-column label="等级" width="50">
        <template #default="scope">
          {{ scope.row[0][6] }}
        </template>
      </el-table-column>
      <el-table-column label="总属性" width="60">
        <template #default="scope">
          {{
            parseInt(scope.row[0][0]) +
              parseInt(scope.row[0][1]) +
              parseInt(scope.row[0][2]) +
              parseInt(scope.row[0][3]) +
              parseInt(scope.row[0][4]) +
              parseInt(scope.row[0][5]) +
              parseInt(scope.row[0][6])
          }}
        </template>
      </el-table-column>
      <el-table-column label="是否升级高级工作">
        <template #default="scope">
          {{ isHighWork(scope.row) }}
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
import { ElMessage } from "element-plus";
import { mapGetters } from "vuex";
export default {
  name: "hero list",
  data: function() {
    return {
      multipleSelection: [],
      addressTransferTo: "",
      isGetRewarding: false,
      isQuitWorking: false,
      isDoLinggong: false,
      isTransfering: false,
    };
  },
  computed: {
    ...mapGetters(["heros"]),
  },
  watch: {
    // heros: function(val, val_old) {
    //   console.log("watch heros", val, val_old);
    // },
  },
  methods: {
    isHighWork: function(hero) {
      const rs =
        hero["roleType"] == "战士" &&
        parseInt(hero[0][0]) > 85 &&
        parseInt(hero[0][2]) > 60
          ? "是"
          : hero["roleType"] == "盗贼" &&
            parseInt(hero[0][1]) > 85 &&
            parseInt(hero[0][0]) > 60
          ? "是"
          : hero["roleType"] == "法师" &&
            parseInt(hero[0][4]) > 85 &&
            parseInt(hero[0][5]) > 60
          ? "是"
          : hero["roleType"] == "游侠" &&
            parseInt(hero[0][0]) > 85 &&
            parseInt(hero[0][1]) > 60
          ? "是"
          : "否";
      return rs;
    },
    shortId: function(tokenId) {
      return `${tokenId.substr(0, 4)}...${tokenId.substr(-4, 4)}`;
    },
    handleSelectionChange: function(val) {
      this.multipleSelection = val;
      console.log("handleSelectionChange rows=", this.multipleSelection);
    },
    filterHandler: function(val, row, col) {
      // console.log(`var=${val}`, val, `row=${row}`, row, `col=${col}`, col);
      if (col["label"] == "类型") {
        return val == row["roleType"];
      }
      if (col["label"] == "工作状态") {
        return val == (row.isWorking ? "工作中" : "休息");
      }
      return false;
    },
    claim: async function() {
      console.log("claim", this.multipleSelection);
      if (this.multipleSelection.length == 0) {
        ElMessage.warning("未选中英雄");
        return;
      }
      let workingPlayerCount = 0;
      let tokens = [];
      for (let i = 0; i < this.multipleSelection.length; i++) {
        const hero = this.multipleSelection[i];
        if (hero.isWorking) {
          workingPlayerCount++;
          tokens.push({ id: hero.tokenId, isMiningOld: hero.isMiningOld });
        }
      }
      if (workingPlayerCount == 0) {
        ElMessage.warning("选中英雄没有任意一个是工作中的");
        return;
      }
      console.log(tokens);
      this.isGetRewarding = true;
      await this.$store.dispatch("mining/getRewardBatch", { tokens });
      this.isGetRewarding = false;
    },
    quitwork: async function() {
      console.log("quitwork", this.multipleSelection);
      if (this.multipleSelection.length == 0) {
        ElMessage.warning("未选中英雄");
        return;
      }
      let workingPlayerCount = 0;
      let tokens = [];
      for (let i = 0; i < this.multipleSelection.length; i++) {
        const hero = this.multipleSelection[i];
        if (hero.isWorking) {
          workingPlayerCount++;
          tokens.push({ id: hero.tokenId, isMiningOld: hero.isMiningOld });
        }
      }
      if (workingPlayerCount == 0) {
        ElMessage.warning("选中英雄没有任意一个是工作中的");
        return;
      }
      console.log(tokens);
      this.isQuitWorking = true;
      await this.$store.dispatch("mining/getQuitworkBatch", { tokens });
      this.isQuitWorking = false;
    },
    doLinggong: async function() {
      console.log("quitwork", this.multipleSelection);
      if (this.multipleSelection.length == 0) {
        ElMessage.warning("未选中英雄");
        return;
      }
      let notworkingPlayerCount = 0;
      let tokens = [];
      for (let i = 0; i < this.multipleSelection.length; i++) {
        const hero = this.multipleSelection[i];
        if (!hero.isWorking) {
          notworkingPlayerCount++;
          tokens.push({ id: hero.tokenId, isMiningOld: hero.isMiningOld });
        }
      }
      if (notworkingPlayerCount == 0) {
        ElMessage.warning("选中英雄没有任意一个是休息中的");
        return;
      }
      console.log(tokens);
      this.isDoLinggong = true;
      await this.$store.dispatch("mining/doLinggongBatch", { tokens });
      this.isDoLinggong = false;
    },
    transferBatch: async function() {
      console.log("transferBatch", this.multipleSelection);
      if (this.multipleSelection.length == 0) {
        ElMessage.warning("未选中英雄");
        return;
      }
      if (this.addressTransferTo == "") {
        ElMessage.warning("请输入接收卡的地址");
        this.$refs.addrTo.focus();
        return;
      }
      let notworkingPlayerCount = 0;
      let tokens = [];
      for (let i = 0; i < this.multipleSelection.length; i++) {
        const hero = this.multipleSelection[i];
        if (!hero.isWorking) {
          notworkingPlayerCount++;
          tokens.push({
            id: hero.tokenId,
            roleType: hero.roleType,
            isMiningOld: hero.isMiningOld,
          });
        }
      }
      if (notworkingPlayerCount == 0) {
        ElMessage.warning("选中英雄没有任意一个是休息中的");
        return;
      }
      console.log(tokens);
      this.$confirm(
        `此操作将会把您勾选的休息中的${tokens.length}张卡片转给${this.addressTransferTo}`,
        "提示",
        {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning",
        }
      ).then(async () => {
        this.isTransfering = true;
        await this.$store.dispatch("mining/doTransferBatch", {
          tokens,
          addressTransferTo: this.addressTransferTo,
        });
        this.isTransfering = false;
      });
    },
  },
};
</script>

<style>
.action {
  text-align: left;
  padding-top: 10px;
  padding-left: 30px;
}
</style>
