const state = {
  addressDatie: "0x4713A70db9AD47780EFC3300c08C17c4013DCa57",
  datie: null,
  addressLinggong: "0xfA65a5751ef6079C1022Aa10b9163d7A2281360A",
  linggong: null,
};
const mutations = {
  SET_DATIE: (state, c) => (state.datie = c),
  SET_LINGGONG: (state, c) => (state.linggong = c),
};
const actions = {
  async contractAt({ rootGetters, state, commit }) {
    commit(
      "SET_DATIE",
      new rootGetters.web3.eth.Contract(
        require(`@/contracts/datie_abi`),
        state.addressDatie
      )
    );
    commit(
      "SET_LINGGONG",
      new rootGetters.web3.eth.Contract(
        require(`@/contracts/linggong_abi`),
        state.addressLinggong
      )
    );
  },
};

export default {
  namespaced: true,
  state,
  mutations,
  actions,
};
