const state = {
  // mining
  addressMining: "0xe278BDF4541cc309b379F9A4E867F60fD6B7BC59",
  contractInstance: null,
  balanceOf: 0,
  tokenIds: [],
  // mining new
  addressMining_new: "0x698E165F2897e4daC68671c4cDFf337bbC543767",
  contractInstance_new: null,
  balanceOf_new: 0,
  tokenIds_new: [],
  // feel
  is_contract_ated: false,
  is_contract_ated_new: false,
};
const mutations = {
  SET_CONTRACT_INSTANCE: (state, c) => {
    state.contractInstance = c;
  },
  SET_CONTRACT_INSTANCE_NEW: (state, c) => {
    state.contractInstance_new = c;
  },
  SET_IS_CONTRACT_ATED: (state, a) => {
    state.is_contract_ated = a;
  },
  SET_IS_CONTRACT_ATED_NEW: (state, a) => {
    state.is_contract_ated_new = a;
  },
  SET_BALANCE_OF: (state, b) => {
    state.balanceOf = b;
  },
  SET_BALANCE_OF_NEW: (state, b) => {
    state.balanceOf_new = b;
  },
  SET_TOKEN_IDS: (state, arr) => {
    state.tokenIds = arr;
  },
  SET_TOKEN_IDS_NEW: (state, arr) => {
    state.tokenIds_new = arr;
  },
};
const actions = {
  async contractAtAll({ state, dispatch }) {
    // console.log("mining contract at with address & abi ", state);
    await dispatch("contractAt", {
      abiName: "ming_abi",
      address: state.addressMining,
      key_ins: "SET_CONTRACT_INSTANCE",
      key_at: "SET_IS_CONTRACT_ATED",
    });
    await dispatch("contractAt", {
      abiName: "new_ming_abi",
      address: state.addressMining_new,
      key_ins: "SET_CONTRACT_INSTANCE_NEW",
      key_at: "SET_IS_CONTRACT_ATED_NEW",
    });
    // at all work contracts
    await dispatch("work/contractAt", {}, { root: true });
  },
  async contractAt(
    { rootGetters, commit },
    { abiName, address, key_ins, key_at }
  ) {
    commit(
      key_ins,
      new rootGetters.web3.eth.Contract(
        require(`@/contracts/${abiName}`),
        address
      )
    );
    commit(key_at, true);
    // console.log("role warrior constract instance ated");
  },
  async getMingingInfoAll({ state, dispatch }, { owner }) {
    console.log("getMingingInfo_all state=", state);
    if (
      state.is_contract_ated == false ||
      state.is_contract_ated_new == false
    ) {
      await dispatch("contractAtAll");
    }
    await dispatch("getMingingInfo", { owner });
    await dispatch("getMingingInfo_new", { owner });
  },
  async getMingingInfo({ dispatch }, { owner }) {
    // console.log("getMingingInfo state=", state);
    await dispatch("balanceOf", { owner });
    await dispatch("getRoleInfo", { owner });
  },
  async balanceOf({ state, commit }, { owner }) {
    const balance = await state.contractInstance.methods
      .balanceOf(owner)
      .call();
    console.log("balance of mining=", balance);
    commit("SET_BALANCE_OF", balance);
  },
  async getRoleInfo({ state, commit }, { owner }) {
    if (state.balanceOf <= 0) return;
    const roleTotal = state.balanceOf;
    let tokenIds = [];
    for (let i = 0; i < roleTotal; i++) {
      const tokenId = await state.contractInstance.methods
        .tokenOfOwnerByIndex(owner, i)
        .call();
      //   console.log("role warrior [", i, "] name=", tokenId);
      tokenIds.push(tokenId);
    }
    commit("SET_TOKEN_IDS", tokenIds);
    console.log("mining  tokenIds=", tokenIds);
    // dispatch("role_detail/getRoleDetail", {}, { root: true });
  },
  async getMingingInfo_new({ dispatch }, { owner }) {
    // console.log("getMingingInfo new state=", state);
    await dispatch("balanceOf_new", { owner });
    await dispatch("getRoleInfo_new", { owner });
  },
  async balanceOf_new({ state, commit }, { owner }) {
    const balance = await state.contractInstance_new.methods
      .balanceOf(owner)
      .call();
    console.log("balance of mining new=", balance);
    commit("SET_BALANCE_OF_NEW", balance);
  },
  async getRoleInfo_new({ state, commit }, { owner }) {
    if (state.balanceOf_new <= 0) return;
    const roleTotal = state.balanceOf_new;
    let tokenIds = [];
    for (let i = 0; i < roleTotal; i++) {
      const tokenId = await state.contractInstance_new.methods
        .tokenOfOwnerByIndex(owner, i)
        .call();
      //   console.log("role warrior [", i, "] name=", tokenId);
      tokenIds.push(tokenId);
    }
    commit("SET_TOKEN_IDS_NEW", tokenIds);
    console.log("mining new  tokenIds=", tokenIds);
    // dispatch("role_detail/getRoleDetail", {}, { root: true });
  },
  async getRewardBatch({ rootState, dispatch }, { tokens }) {
    const sender = rootState.web3s.address_eth;
    for (let i = 0; i < tokens.length; i++) {
      const token = tokens[i];
      let miningContract = null;
      if (token.isMiningOld) {
        miningContract = rootState.mining.contractInstance;
      } else {
        miningContract = rootState.mining.contractInstance_new;
      }
      try {
        await miningContract.methods.getAward(token.id).send({ from: sender });
      } catch (err) {
        // console.log(err);
        if (err.code == 4001) {
          console.log(`拒绝收菜 tokenId=${token.id}`);
        }
      }
    }
    await dispatch("hero/search", { owner: sender }, { root: true });
  },
  async getQuitworkBatch({ rootState, dispatch }, { tokens }) {
    const sender = rootState.web3s.address_eth;
    for (let i = 0; i < tokens.length; i++) {
      const token = tokens[i];
      let miningContract = null;
      if (token.isMiningOld) {
        miningContract = rootState.mining.contractInstance;
      } else {
        miningContract = rootState.mining.contractInstance_new;
      }
      try {
        await miningContract.methods.quitWork(token.id).send({ from: sender });
      } catch (err) {
        // console.log(err);
        if (err.code == 4001) {
          console.log(`拒绝退出工作 tokenId=${token.id}`);
        }
      }
    }
    await dispatch("hero/search", { owner: sender }, { root: true });
  },
  async doLinggongBatch({ rootState, dispatch }, { tokens }) {
    const sender = rootState.web3s.address_eth;
    for (let i = 0; i < tokens.length; i++) {
      const token = tokens[i];
      let miningContract = null;
      if (token.isMiningOld) {
        miningContract = rootState.mining.contractInstance;
      } else {
        miningContract = rootState.mining.contractInstance_new;
      }
      try {
        await miningContract.methods
          .work(rootState.work.addressLinggong, token.id)
          .send({ from: sender });
      } catch (err) {
        // console.log(err);
        if (err.code == 4001) {
          console.log(`拒绝派去打零工 tokenId=${token.id}`);
        }
      }
    }
    await dispatch("hero/search", { owner: sender }, { root: true });
  },
  async doTransferBatch(
    { rootState, dispatch },
    { tokens, addressTransferTo }
  ) {
    const sender = rootState.web3s.address_eth;
    for (let i = 0; i < tokens.length; i++) {
      const token = tokens[i];
      let roleContract = null;
      if (token.roleType == "战士") {
        roleContract = rootState.role_warrior.contractInstance;
      } else if (token.roleType == "盗贼") {
        roleContract = rootState.role_robber.contractInstance;
      } else if (token.roleType == "法师") {
        roleContract = rootState.role_mage.contractInstance;
      } else if (token.roleType == "游侠") {
        roleContract = rootState.role_yx.contractInstance;
      }
      try {
        await roleContract.methods
          .transferFrom(sender, addressTransferTo, token.id)
          .send({ from: sender });
      } catch (err) {
        // console.log(err);
        if (err.code == 4001) {
          console.log(`拒绝转卡 tokenId=${token.id}`);
        }
      }
    }
    await dispatch("hero/search", { owner: sender }, { root: true });
  },
};
export default {
  namespaced: true,
  state,
  mutations,
  actions,
};
